import {PerfGraph} from "./perf-graph";
import {PerfEntityStateData} from "@generated/perf-entity-state-data";
import {Logger} from "@util/logger";
import {PerfEntityIdentifier} from "./perf-entity-identifier";

export class GraphMetric {

   private static readonly GRAPH_ID_TOKEN_SEPARATOR: string = ".";
   static readonly ENTITY_REF_ID_TOKEN_SEPARATOR: string = ":";

   private _metrics: PerfEntityStateData;
   private _graphs: PerfGraph[];

   /*
   * Keeps entity specific data
   * */
   entityIdentifier: PerfEntityIdentifier;

   constructor(metrics: PerfEntityStateData, graphs: PerfGraph[], public entityRefId: string = "") {
      this._metrics = metrics;
      this._graphs = GraphMetric.getRelevantGraphs(entityRefId, metrics, graphs);
   }

   get metrics(): PerfEntityStateData {
      return this._metrics;
   }

   get graphs(): PerfGraph[] {
      return this._graphs;
   }

   private static getMetricIds(metrics: PerfEntityStateData): Set<string> {
      if (!metrics || !metrics.metricsSeries) {
         return new Set<string>();
      }

      const metricIds: string[] = [];
      metricIds.push(...metrics.metricsSeries.map(val => {
         return val.key;
      }));

      return new Set<string>(metricIds);
   }

   private static getRelevantGraphs(entityRefId: string, metrics: PerfEntityStateData, graphs: PerfGraph[]): any[] {
      if (!graphs || !metrics) {
         return [];
      }

      const metricIds: Set<string> = GraphMetric.getMetricIds(metrics);

      // A normal ID looks like this "host-domclient:59125660-8835-8f26-492d-000c2999863a"
      // In the perf support view, it's like "59125660-8835-8f26-492d-000c2999863a"
      // Aggregated charts entityRefId looks like "host-domclinet/host-diskgroup"
      // We want only the entity's type information without the ID string
      const entitySimplifiedId = entityRefId ? entityRefId.split(GraphMetric.ENTITY_REF_ID_TOKEN_SEPARATOR)[0] : null;

      return graphs.filter(graph => {
         return graph.metrics.some(metric => {
            if (entitySimplifiedId) {
               const graphId: string = GraphMetric.getSimplifiedGraphId(graph);
               return graphId === entitySimplifiedId && metricIds.has(metric.key);
            } else {
               return metricIds.has(metric.key);
            }
         });
      });
   }

   /**
    * The graph's ID is in the form of a package name (e.g: "com.vmware.vsan.perf.graph.vscsi.iops").
    * The important part are the last 2 tokens holding information about the entity type and the type of
    * data being profiled.
    * @param graph The graph which ID is going to be simplified
    * @returns {string} The simplified version of the ID without the common prefix.
    */
   private static getSimplifiedGraphId(graph: any): string {
      if (!graph.id) {
         return null;
      }

      let tokens = graph.id.split(GraphMetric.GRAPH_ID_TOKEN_SEPARATOR);
      if (tokens.length < 7) {
         Logger.warn("Unknown format of graph's ID: " + graph.id);
         return null;
      }

      return tokens[5];
   }
}