import {PerfChartDataConstants} from "@component/chart/performance/perf-chart-util";
import {TreeNode} from "@component/inventory/tree-view/tree-node";
import {MountedRemoteDatastore} from "@generated/mounted-remote-datastore";
import {PerfMonitorCommonPropsData} from "@generated/perf-monitor-common-props-data";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {VsanUiUtils} from "@util/vsan-util";
import {BasePerfEntity} from "../model/base-perf-entity";
import {NestedPerfEntity} from "../model/nested-perf-entity";
import {PerfMonitorViewType} from "../model/perf-monitor-view-type.model";

export class PerformanceUtils {

   /**
    * This method is only for single entity views because in single entity view the entityId-view mapping is fixed.
    * In multi-entities view, the entities listed in the metrics may have different entityIds,
    * for example Host > Disks view, there are 2 entityIds, cache-disk and capacity-disk
    */
   static getEntityTypeIdByPerfView(view: PerfMonitorViewType): string {
      switch (view) {
         case PerfMonitorViewType.CLUSTER_VM_CONSUMPTION:
            return PerfChartDataConstants.CLUSTER_VM_CONSUMPTION_ENTITY;
         case PerfMonitorViewType.CLUSTER_VM_REMOTE_CONSUMPTION:
            return PerfChartDataConstants.CLUSTER_VM_CONSUMPTION_REMOTE_ENTITY;
         case PerfMonitorViewType.CLUSTER_COMPUTE_ONLY_VM_REMOTE_CONSUMPTION:
            return PerfChartDataConstants.CLUSTER_COMPUTE_ONLY_VM_REMOTE_CONSUMPTION_ENTITY;
         case PerfMonitorViewType.CLUSTER_BACKEND:
            return PerfChartDataConstants.CLUSTER_BACKEND_ENTITY;
         case PerfMonitorViewType.CLUSTER_FILE_SERVICE:
            return PerfChartDataConstants.CLUSTER_FILE_SERVICE_ENTITY;
         case PerfMonitorViewType.CLUSTER_IO_INSIGHT:
            return PerfChartDataConstants.IO_INSIGHT_ENTITY;
         case PerfMonitorViewType.CLUSTER_PMEM:
            return PerfChartDataConstants.PMEM_CLUSTER_ENTITY;
         case PerfMonitorViewType.CLUSTER_VSAN_DIRECT:
            return PerfChartDataConstants.CLUSTER_VSAN_DIRECT_ENTITY;

         case PerfMonitorViewType.HOST_VM_CONSUMPTION:
            return PerfChartDataConstants.HOST_VM_CONSUMPTION_ENTITY;
         case PerfMonitorViewType.HOST_BACKEND:
            return PerfChartDataConstants.HOST_BACKEND_ENTITY;
         case PerfMonitorViewType.HOST_ISCSI:
            return PerfChartDataConstants.HOST_ISCSI_ENTITY;
         case PerfMonitorViewType.HOST_IO_INSIGHT:
            return PerfChartDataConstants.IO_INSIGHT_ENTITY;
         case PerfMonitorViewType.HOST_VSAN_DIRECT:
            return PerfChartDataConstants.HOST_VSAN_DIRECT_ENTITY;
         case PerfMonitorViewType.HOST_PMEM:
            return PerfChartDataConstants.PMEM_HOST_ENTITY;

         case PerfMonitorViewType.VM_CONSUMPTION:
            return PerfChartDataConstants.VM_CONSUMPTION_ENTITY;
         default:
            return null;
      }
   }

   static getSpecifiedEntityIdByPerfView(view: PerfMonitorViewType): string {
      return new Map([
         [PerfMonitorViewType.CLUSTER_VM_CONSUMPTION, PerfChartDataConstants.VM_CONSUMPTION_ENTITY],
         [PerfMonitorViewType.HOST_VM_CONSUMPTION, PerfChartDataConstants.VM_CONSUMPTION_ENTITY],
      ]).get(view);
   }

   static isMultipleEntityPerfSupportedByView(view: PerfMonitorViewType): boolean {
      return [
         PerfMonitorViewType.CLUSTER_VM_CONSUMPTION,
         PerfMonitorViewType.HOST_VM_CONSUMPTION,
      ].includes(view);
   }

   static getPerfViewSelectOptionLabel(view: PerfMonitorViewType): string {
      switch (view) {
         case PerfMonitorViewType.CLUSTER_VM_CONSUMPTION:
            return VsanUiUtils.getString("vsan.monitor.cluster.performance.vmConsumption.title");
         case PerfMonitorViewType.CLUSTER_VM_REMOTE_CONSUMPTION:
            return VsanUiUtils.getString("vsan.monitor.cluster.performance.vmConsumptionRemote.title");
         case PerfMonitorViewType.CLUSTER_COMPUTE_ONLY_VM_REMOTE_CONSUMPTION:
            return VsanUiUtils.getString("vsan.monitor.cluster.performance.computeClusterVmConsumptionRemote.title");
         case PerfMonitorViewType.CLUSTER_BACKEND:
            return VsanUiUtils.getString("vsan.monitor.cluster.performance.backend.title");
         case PerfMonitorViewType.CLUSTER_ISCSI:
            return VsanUiUtils.getString("vsan.monitor.cluster.performance.iscsi.title");
         case PerfMonitorViewType.CLUSTER_FILE_SERVICE:
            return VsanUiUtils.getString("vsan.monitor.cluster.performance.fileServices.title");
         case PerfMonitorViewType.CLUSTER_IO_INSIGHT:
            return VsanUiUtils.getString("vsan.monitor.cluster.performance.io.insight");
         case PerfMonitorViewType.CLUSTER_PMEM:
            return VsanUiUtils.getString("vsan.monitor.cluster.performance.pmem.title");
         case PerfMonitorViewType.CLUSTER_VSAN_DIRECT:
            return VsanUiUtils.getString("vsan.monitor.cluster.performance.vsanDirect.title");
         case PerfMonitorViewType.HOST_VM_CONSUMPTION:
            return VsanUiUtils.getString("vsan.monitor.host.performance.vmConsumption.title");
         case PerfMonitorViewType.HOST_BACKEND:
            return VsanUiUtils.getString("vsan.monitor.host.performance.backend.title");
         case PerfMonitorViewType.HOST_DISK:
            return VsanUiUtils.getString("vsan.monitor.host.performance.disk.title");
         case PerfMonitorViewType.HOST_PHYSICAL_ADAPTER:
            return VsanUiUtils.getString("vsan.monitor.host.performance.physicalAdapter.title");
         case PerfMonitorViewType.HOST_VIRTUAL_ADAPTER:
            return VsanUiUtils.getString("vsan.monitor.host.performance.virtualAdapter.title");
         case PerfMonitorViewType.HOST_ISCSI:
            return VsanUiUtils.getString("vsan.monitor.host.performance.iscsi.title");
         case PerfMonitorViewType.HOST_IO_INSIGHT:
            return VsanUiUtils.getString("vsan.monitor.host.performance.io.insight");
         case PerfMonitorViewType.HOST_VSAN_DIRECT:
            return VsanUiUtils.getString("vsan.monitor.host.performance.vsanDirect.title");
         case PerfMonitorViewType.HOST_PMEM:
            return VsanUiUtils.getString("vsan.monitor.host.performance.pmem.title");
         case PerfMonitorViewType.VM_CONSUMPTION:
            return VsanUiUtils.getString("vsan.monitor.vm.performance.vmConsumption.title");
         case PerfMonitorViewType.VM_VIRTUAL_DISK:
            return VsanUiUtils.getString("vsan.monitor.vm.performance.virtualDisk.title");
         default:
            Logger.error("Invalid view type: ", view);
            return null;
      }
   }

   static isSingleEntityViewSelected(view: PerfMonitorViewType): boolean {
      return [
         PerfMonitorViewType.CLUSTER_VM_CONSUMPTION,
         PerfMonitorViewType.CLUSTER_VM_REMOTE_CONSUMPTION,
         PerfMonitorViewType.CLUSTER_COMPUTE_ONLY_VM_REMOTE_CONSUMPTION,
         PerfMonitorViewType.CLUSTER_BACKEND,
         PerfMonitorViewType.CLUSTER_PMEM,
         PerfMonitorViewType.CLUSTER_VSAN_DIRECT,
         PerfMonitorViewType.HOST_VM_CONSUMPTION,
         PerfMonitorViewType.HOST_BACKEND,
         PerfMonitorViewType.HOST_ISCSI,
         PerfMonitorViewType.HOST_VSAN_DIRECT,
         PerfMonitorViewType.HOST_PMEM,
         PerfMonitorViewType.VM_CONSUMPTION,
      ].includes(view);
   }

   static getPerfEntityByUuid(entities: BasePerfEntity[], uuid: string): BasePerfEntity {
      // Perf API returns UUIDs in format -> entityType:guid.
      // Entity type should be removed and search guid in collection.
      return entities.find(entity => entity.entityUuid === uuid.split(":")[1]);
   }

   static getRemoteClustersTreeNodes(mountedRemoteDatastores: MountedRemoteDatastore[]): TreeNode[] {
      if (!mountedRemoteDatastores) {
         return [];
      }
      return mountedRemoteDatastores
            .filter(ds => !ds.isLocal)
            .filter(PerformanceUtils.isRemoteClusterUnique)
            .map(ds => new TreeNode(ds.shareableDatastore.serverCluster, null));
   }

   static isRemoteClusterUnique(item: MountedRemoteDatastore, index: number, self: MountedRemoteDatastore[]): boolean {
      return self.findIndex(current => ManagedObject.areEqual(current.shareableDatastore.serverCluster.moRef,
            item.shareableDatastore.serverCluster.moRef)) === index;
   }

   /**
    * Returns a message if the difference between client browser time and Master Node host time is more than 1 hour.
    * Otherwise return null.
    */
   static validateMasterNodeTime(currentTimeOnMasterNode: number): string {
      if (currentTimeOnMasterNode && VsanDateTimeUtils.isMoreThanOneHourAgoFromNow(currentTimeOnMasterNode)) {
         return VsanUiUtils.getString("vsan.perf.service.chart.time.synch.warning");
      }
      return null;
   }

   static updatePerfEntityType(perfEntity: BasePerfEntity, commonPerfData: PerfMonitorCommonPropsData): void {
      perfEntity.configSpec = commonPerfData.entityTypes[perfEntity.entityId];
      if (perfEntity instanceof NestedPerfEntity) {
         perfEntity.combinedEntity.configSpec = commonPerfData.entityTypes[perfEntity.combinedEntity.entityId];
      }
   }
}
