/**
 * Represents the zoom level of a graph
 */
export class ChartZoomOption {

   /**
    * The start of the selected range (left border)
    */
   min: number;

   /**
    * The end of the selected range (right border)
    */
   max: number;

   /**
    * First element index of the chart's data
    */
   dataMin: number;

   /**
    * Last element index of the chart's data
    */
   dataMax: number;

   isZoomOut: boolean;

}