/**
 * Model holding information for the selected points on a plotLine
 */
export class ChartPlotLineSelectionData {
   public graphId: string;

   /**
    * The actual category value, i.e. the value of XAxis at the selected plotLine. In most cases this would be
    * the time stamp.
    */
   public timePoint: string;

   /**
    * The values of each series at the selected plotLine.
    */
   public seriesValues: ChartMetricSelectionData[];
}

export class ChartMetricSelectionData {
   public metricLabel: string;
   public metricKey: string;
   public value: number;
}