import {AfterViewInit, Component, OnInit, ViewChild} from "@angular/core";
import {IdGenerator} from "@util/id-generator";
import {VsanUiUtils} from "@util/vsan-util";
import {PerfChartContextData} from "@component/chart/performance/perf-chart-context.data";
import {ChartZoomOption} from "@component/chart/performance/chart-zoom-option";
import {FlatGraphMetric} from "@component/chart/performance/flat-graph-metric";
import {PerfChartUtil} from "@component/chart/performance/perf-chart-util";
import {PerfChartComponent} from "@component/chart/performance/perf-chart.component";
import {PerfGraphThreshold} from "@generated/perf-graph-threshold";

@Component({
   templateUrl: "perf-chart-dialog.html",
   selector: "vsan-perf-chart-dialog",
   styleUrls: ["perf-chart-dialog.scss"],
})
export class PerfChartDialogComponent implements OnInit, AfterViewInit {
   private readonly BOTTOM_MIDDLE_POSITION: string = "bottom-middle";
   private readonly CHART_HEIGHT: number = 490;
   private readonly CHART_WIDTH: number = 1150;

   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;

   id: string;
   isShowZoomButton: boolean = true;
   isZoomed: boolean;
   isShowGraphDescription: boolean;
   zoomOptions: ChartZoomOption;
   entry: FlatGraphMetric;
   looseXAxisLabel: boolean = false;
   chartLegendPosition: string = PerfChartUtil.LEGEND_BOTTOM;
   customizableThresholds: PerfGraphThreshold[];

   @ViewChild(PerfChartComponent)
   perfChart: PerfChartComponent;

   ngOnInit(): void {
      this.id = IdGenerator.getGuid();
      const context: PerfChartContextData = VsanUiUtils.getModalContext() as PerfChartContextData;
      this.entry = context.entry;
      this.isShowZoomButton = context.isShowZoomButton;
      this.isShowGraphDescription = context.isShowGraphDescription;
      this.looseXAxisLabel = context.looseXAxisLabel;
      this.chartLegendPosition = context.chartLegendPosition;
      this.zoomOptions = context.zoomOptions;
      this.isZoomed = this.zoomOptions && !this.zoomOptions.isZoomOut;
      this.customizableThresholds = context.customizableThresholds;
   }

   ngAfterViewInit(): void {
      if (this.perfChart) {
         if (this.perfChart.graph && !this.perfChart.graph.isHistogram && this.zoomOptions) {
            this.perfChart.chart.xAxis[0].setExtremes(this.zoomOptions.min, this.zoomOptions.max);
         }
         this.perfChart.chart.setSize(this.CHART_WIDTH, this.CHART_HEIGHT);
      }
   }

   collapseClicked(): void {
      const zoomOptions: ChartZoomOption = this.perfChart.graph.isHistogram ? null : this.zoomOptions;
      VsanUiUtils.closeModalDialog(zoomOptions);
   }

   onChartZoomIn(opt: ChartZoomOption): void {
      this.isZoomed = !opt.isZoomOut;
      this.zoomOptions = opt;
   }

   /**
    * Reset the zoom level to default
    * @param event
    */
   onZoomOut(): void {
      const zoomOut: ChartZoomOption = new ChartZoomOption();
      zoomOut.isZoomOut = true;
      this.zoomOptions = zoomOut;
   }

   graphTooltipVisible = (entry: FlatGraphMetric): boolean => {
      return this.isShowGraphDescription && !!entry.graph.description;
   };

   getPopUpPosition = (): string => {
      return this.BOTTOM_MIDDLE_POSITION;
   };
}
