import {BehaviorSubject} from "rxjs";
import {BasePerfEntity} from "../../../performance/model/base-perf-entity";
import {GraphMetric} from "@component/chart/performance/graph-metrics.data";
import {PerfGraph} from "@component/chart/performance/perf-graph";
import {PerfTimeRangeData} from "@generated/perf-time-range-data";
import {PerfConsolidatedMetricSeries} from "@component/chart/performance/perf-consolidated-metric-series";
import {PerfChangeContext, PerfData} from "./perf-data";
import {ConsolidatedViewHelper} from "@util/performance/consolidated-view-helper";
import {PerformanceUtils} from "../../../performance/util/perf-utils";
import {Injectable} from "@angular/core";

// TODO [kaleksandrov] Delete this whole file once all its usages are removed

export enum PerfModeType {
   SHOW_SPECIFICS = "show-specifics",
   SHOW_AVERAGE = "show-average",
   TOP_CONTRIBUTORS = "show-top-contributors",
}

export enum PerfModeState {
   SPECIFIC_MODE_SHOW_CONSOLIDATED = "show-consolidated",
   SPECIFIC_MODE_SHOW_SEPARATELY = "show-separately",
}

/**
 * This class is responsible for orchestrate multiple entities performance data
 */
@Injectable()
export class PerfOrchestratorService {
   /*
   * Keeps all needed data for properly displaying multiple entities performance data
   * Such as entity models, chart data, metrics etc.
   */
   data: PerfData;

   /*
   * When there is some change event on performance modes, states or data then this is triggered in order to inform
   * components responsible for fetching and displaying performance data
   */
   change: BehaviorSubject<PerfChangeContext>;

   /*
   * This helper is used for keeping all functionality for enable/disable checkboxes when consolidated view is enabled
   */
   consolidatedViewHelper: ConsolidatedViewHelper;

   /*
   * Keeps count of how much entities are still fetching from perf API
   */
   remainingEntitiesToLoad: number = 0;

   get isLoading(): boolean {
      return this.remainingEntitiesToLoad > 0;
   }

   /*
   * There is 2 modes in order to show one view for average parent metrics
   * and one for specified child entities metrics
   */
   private _mode: PerfModeType;

   /*
   * Every mode has different states. For example SHOW_SPECIFICS mode has 2 states in order to show one view
   * for consolidated metrics and one for separately display for selected entities metrics
   */
   private _state: PerfModeState = PerfModeState.SPECIFIC_MODE_SHOW_CONSOLIDATED;

   private STATES = [];

   constructor() {
      this.STATES[PerfModeType.SHOW_SPECIFICS] = [
         PerfModeState.SPECIFIC_MODE_SHOW_CONSOLIDATED,
         PerfModeState.SPECIFIC_MODE_SHOW_SEPARATELY,
      ];

      this.change = new BehaviorSubject(null);

      this._mode = PerfModeType.SHOW_AVERAGE;

      this.data = new PerfData(this.change);
      this.consolidatedViewHelper = new ConsolidatedViewHelper(this.change, this.data);
   }

   onEntityPerfDataLoaded(loadedDataCount: number = 1): void {
      /*
      * When entity perf data is loaded for entity subtract 1 from remainingEntitiesToLoad in order to indicate that
      * entity data is loaded
      */
      this.remainingEntitiesToLoad -= loadedDataCount;

      /*
      * If no remaining entities perf data to be loaded then stop loading
      */
      if (this.remainingEntitiesToLoad <= 0) {
         this.remainingEntitiesToLoad = 0;
      }
   }

   get isShowSpecificModeEnabled(): boolean {
      return this.mode === PerfModeType.SHOW_SPECIFICS;
   }

   get isConsolidatedViewStateEnabled(): boolean {
      return this.state === PerfModeState.SPECIFIC_MODE_SHOW_CONSOLIDATED;
   }

   get isSeparatlyViewStateEnabled(): boolean {
      return this.state === PerfModeState.SPECIFIC_MODE_SHOW_SEPARATELY;
   }

   get isShowAverageModeEnabled(): boolean {
      return this.mode === PerfModeType.SHOW_AVERAGE ||
            this.mode === PerfModeType.TOP_CONTRIBUTORS;
   }

   get mode(): PerfModeType {
      return this._mode;
   }

   set mode(value: PerfModeType) {
      if (this._mode === value) {
         return;
      }
      this._mode = value;

      this.change.next(PerfChangeContext.MODE_CHANGED);
   }

   get state(): PerfModeState {
      return this._state;
   }

   set state(value: PerfModeState) {
      if (this._state === value || this.STATES[this._mode][value] != null) {
         return;
      }
      this._state = value;
      this.change.next(PerfChangeContext.STATE_CHANGED);
   }

   get entityGraph(): PerfGraph {
      return this.data.entityTypes[this.data.entityType];
   }

   setSelectedSpecifiedEntities() {
      if (this.data.entities == null || this.data.entitySelectionStateMap == null) {
         return;
      }
      this.data.selectedEntities = this.data.entities
            .filter((entity: BasePerfEntity) => this.data.entitySelectionStateMap.get(entity.entityIdentifier));
   }

   getEntityByVsanUuid(uuid: string): BasePerfEntity {
      return PerformanceUtils.getPerfEntityByUuid(this.data.entities, uuid);
   }

   getEntityGraphDataById(id: string): GraphMetric[] {
      if (!this.data.entitiesGraphData) {
         return;
      }

      return this.data.entitiesGraphDataCache[id];
   }

   getSpecifiedEntitiesFlatDataByGraph = (graph: PerfGraph): PerfConsolidatedMetricSeries[] => {
      if (this.mode === PerfModeType.SHOW_AVERAGE ||
            this.state === PerfModeState.SPECIFIC_MODE_SHOW_SEPARATELY) {
         return null;
      }

      return this.data.getSpecifiedEntitiesFlatDataByGraph(graph);
   };

   /**
    * Reset to default state of multiple entity performance feature
    * More detailed:
    * - Set mode to Show_Average
    * - Clear all selected entities
    * - Update selection
    */
   reset(): void {
      this.mode = PerfModeType.SHOW_AVERAGE;
      this.data.entitySelectionStateMap.clear();
      this.consolidatedViewHelper.entityVisibilityStateMap = {};
      this.setSelectedSpecifiedEntities();
   }

   changeTimeRange(timeRange: PerfTimeRangeData) {
      this.data.timeRangeData = timeRange;
      this.change.next(PerfChangeContext.TIME_RANGE_CHANGED);
   }
}
