import {
   AfterViewInit,
   Component,
   ElementRef,
   EventEmitter,
   HostListener,
   Input,
   OnDestroy,
   Output,
   ViewChild,
} from "@angular/core";
import {HighchartsPropertiesService} from "@component/chart/highcharts-properties.service";
import {
   ChartMetricSelectionData,
   ChartPlotLineSelectionData,
} from "@component/chart/model/chart-plot-line-selection.data";
import {ConsolidatedConfigData} from "@component/chart/model/consolidated-config.data";
import {PefGraphId} from "@component/chart/model/perf-graph-id";
import {PlotLineClickCallback} from "@component/chart/model/plot-line-click-callback";
import {FlatGraphMetric} from "@component/chart/performance/flat-graph-metric";
import {PerfChartPoint} from "@component/chart/performance/perf-chart-point";
import {PerfChartSeries} from "@component/chart/performance/perf-chart-series";
import {SeriesPointHovered} from "@component/chart/performance/series-point-hovered";
import {SetThresholdContextData} from "@component/chart/performance/set-threshold-context.data";
import {PerfEntityStateData} from "@generated/perf-entity-state-data";
import {PerfGraphThreshold} from "@generated/perf-graph-threshold";
import {ManagedObject} from "@service/managed-object";
import {ModalBuilder} from "@util/modal-builder";
import {PerfOrchestratorService} from "@util/performance/perf-orchestrator-service";
import {ReferenceWatcher} from "@util/reference-watcher";
import {VsanUiUtils} from "@util/vsan-util";
import {Point, SeriesOptionsType} from "highcharts";
import * as Highcharts from "highcharts/highcharts";
import ExportData from "highcharts/modules/export-data";
import HighchartsExporting from "highcharts/modules/exporting";
import OfflineExporting from "highcharts/modules/offline-exporting";
import {ConsolidatedEntitiesData} from "../../../../performance/view/consolidated-entities-data.data";
import {HighchartsThemeService} from "../highcharts-theme.service";
import {ChartZoomOption} from "./chart-zoom-option";
import {PerfChartUtil} from "./perf-chart-util";
import {PerfConsolidatedMetricSeries} from "./perf-consolidated-metric-series";
import {PerfGraph} from "./perf-graph";

HighchartsExporting(Highcharts);
OfflineExporting(Highcharts);
ExportData(Highcharts);

@Component({
   templateUrl: "perf-chart.html",
   selector: "vsan-perf-chart",
   styleUrls: ["perf-chart.scss"],
   providers: [ReferenceWatcher],
})
export class PerfChartComponent implements AfterViewInit, OnDestroy {

   @ViewChild("container")
   private chartContainer: ElementRef;

   @ViewChild("chart")
   private chartElement: ElementRef;

   /**
    * Chart configuration object
    */
   options: Highcharts.Options;

   private zoomOptions: ChartZoomOption;

   /**
    * The graph definition
    */
   @Input()
   graph: PerfGraph;

   @Input()
   entityRefId: string;

   @Input()
   containerId: string = "";

   @Input()
   showSmall: boolean = false;

   @Input()
   index: number = 0;

   @Input()
   tooltipVisible: boolean = true;

   @Input()
   tooltipPopUpPosition: string;

   @Input()
   showChartTitle: boolean = true;

   @Input()
   showExpandButton: boolean = true;

   @Input()
   showExportButton: boolean = true;

   private _metrics: PerfEntityStateData;
   /**
    * Metrics to be shown
    */
   @Input()
   set metrics(val: PerfEntityStateData) {
      if (this.isRealtime && this.chart) {
         this.drawNewPoints(val);
      } else {
         this._metrics = val;
      }
   }

   get metrics(): PerfEntityStateData {
      return this._metrics;
   }

   /**
    * Metrics to be shown consolidated (in same charts for more then 1 entity)
    */
   @Input()
   consolidatedMetrics: PerfConsolidatedMetricSeries[];

   /**
    * Holds the data needed for showing multiple entities data into a single chart. This is used for
    * consolidated VM view and File Shares view
    */
   @Input()
   consolidatedEntitiesData: ConsolidatedEntitiesData;

   private _customizableThreshold: PerfGraphThreshold;

   @Input()
   set customizableThresholds(value: PerfGraphThreshold[]) {
      this._customizableThreshold = PerfChartUtil.getCustomizableThreshold(this.graph, value);
   }

   /**
    * Emits a ChartZoomOption object when the chart has been zoomed.
    */
   @Output()
   zoom: EventEmitter<ChartZoomOption> = new EventEmitter<ChartZoomOption>(true);

   @Output()
   expand: EventEmitter<void> = new EventEmitter<void>();

   @Output()
   onPlotLineChanged: EventEmitter<ChartPlotLineSelectionData> = new EventEmitter<ChartPlotLineSelectionData>();

   @Output()
   onTooltipActionClicked: EventEmitter<ChartPlotLineSelectionData> = new EventEmitter<ChartPlotLineSelectionData>();

   private _selectedPlotLineValue: string;

   @Input()
   set selectedPlotLineValue(value: string) {
      if (value === this._selectedPlotLineValue) {
         return;
      }
      this._selectedPlotLineValue = value;
      if (this.chart) {
         this.updatePlotLine();
      }
   }

   @Input()
   chartWidth: number;

   @Input()
   chartHeight: number;

   @Input()
   isRealtime: boolean;

   get showSetThresholdButton(): boolean {
      return !!this._customizableThreshold;
   }

   private static readonly SMALL_CHART_WIDTH: number = 240;

   private static readonly SMALL_CHART_HEIGHT: number = 200;

   private static readonly CONSOLIDATED_CHART_HEIGHT: number = 400;

   private static readonly WAIT_FOR_CHART_CONTAINER_TIME: number = 100; // 0.1 seconds

   /**
    * Emits when the chart finishes loading.
    */
   @Output()
   load: EventEmitter<Highcharts.Chart> = new EventEmitter<Highcharts.Chart>();

   /**
    * Sets the zoom level of the current chart.
    */
   @Input()
   set extremes(zoomOption: ChartZoomOption) {
      if (!zoomOption || !this.isZoomAllowed) {
         return;
      }

      // if the chart is still not created, keep the zoomOptions and update it on drawChart
      if (!this.chart || Object.keys(this.chart).length === 0) {
         this.zoomOptions = zoomOption;
         return;
      }

      // Prevent zooming ioInsight charts because of an issue
      if (this.graph && this.graph.isHistogram) {
         return;
      }

      const xExtremes: Highcharts.ExtremesObject = this.chart.xAxis[0].getExtremes();

      // Revert zoom
      if (zoomOption.isZoomOut) {
         this.chart.xAxis[0].setExtremes(xExtremes.dataMin, xExtremes.dataMax);
         return;
      }

      // Check if the zoomed chart has different data length, if true then don't zoom
      if (zoomOption.dataMin !== xExtremes.dataMin || zoomOption.dataMax !== xExtremes.dataMax) {
         return;
      }

      // Use default values from the charts configuration
      let min: number = xExtremes.dataMin;
      let max: number = xExtremes.dataMax;

      // If the given min value is suitable, use it
      if (zoomOption.min && xExtremes.dataMin < zoomOption.min) {
         min = zoomOption.min;
      }

      // If the given max value is suitable, use it
      if (zoomOption.max && xExtremes.dataMax > zoomOption.max) {
         max = zoomOption.max;
      }

      // Update the zoom level
      this.chart.xAxis[0].setExtremes(min, max);
   }

   /**
    * Indicates if the chart is very small, currently only used for the performance for support view
    */
   @Input()
   looseXAxisLabel: boolean = false;

   @Input()
   legendPosition: string = PerfChartUtil.LEGEND_BOTTOM;

   @Input()
   isZoomAllowed: boolean = true;

   @Input()
   keepPlotLineOnClick: boolean = false;

   /**
    * The link to be shown in the tooltip
    */
   @Input()
   actionLink: string;

   @Output()
   pointHovered: EventEmitter<SeriesPointHovered> = new EventEmitter<SeriesPointHovered>(true);

   @Input()
   set pointHoveredOption(val: SeriesPointHovered) {
      if (!this.chart || this.chart.container.id === val.chartId) {
         return;
      }
      let points: Point[] = [];
      if (val.isSharedTooltip) {
         // For shared tooltip get all series points for the particular x-point.
         points = this.chart.series?.map(chartSeries => chartSeries?.points[val.xIndex]);
      } else {
         points = [this.chart.series[val.seriesIndex]?.points[val.xIndex]];
      }
      /** Filter:
       *  - undefined points to avoid NPE.
       *  - points with different x-axis labels.
       */
      points = points.filter(point => !!point && point.category === val.xLabel);
      if (points.length) {
         const chart: any = points[0].series.chart;
         if (val.isHover) {
            // For shared tooltips use array of points, otherwise a single point. If not an error will be thrown.
            const pointOrPoints: Point | Point[] = val.isSharedTooltip
                  ? points
                  : points[0];
            // Show tooltip (point is automatically hovered) and crossHair.
            chart.tooltip.refresh(pointOrPoints);
            chart.xAxis[0].drawCrosshair(null, pointOrPoints);
         } else {
            points.forEach(point => point.setState(""));
            chart.tooltip.hide();
         }
      }
   }

   chart: Highcharts.Chart;

   constructor(private highchartsThemeService: HighchartsThemeService,
         private highchartsService: HighchartsPropertiesService,
         private perfOrchestrator: PerfOrchestratorService,
         private refWatcher: ReferenceWatcher) {
      this.refWatcher.watchSubscriptionRef(
            this.highchartsThemeService.whenThemeChanged.subscribe(() => {
               this.applyHighChartThemeOptions();
               if (this.chart) {
                  this.chart.update(this.options);
               }
            }));
   }

   async ngAfterViewInit(): Promise<void> {
      this.setChartData();
      this.options = this.buildOptions(this.afterSetExtremesXAxisCallback,
            this._customizableThreshold,
            this.seriesMouseOverCallback,
            this.seriesMouseOutCallback);
      this.applyHighChartThemeOptions();
      if (!this.isZoomAllowed) {
         delete (this.options.chart.zoomType);
      }
      await this.drawChart();

      this.updatePlotLine();
   }

   /**
    * Chart data is needed for QE tests, related to chart's verification
    */
   private setChartData(): void {
      this.chartContainer.nativeElement.chartData = {
         metrics: !this.metrics
               ? this.consolidatedMetrics
               : this.metrics,
         isConsolidated: !!this.consolidatedMetrics,
         graph: this.graph,
         customizableThreshold: this._customizableThreshold,
      };

      // Set timestamps when consolidated mode is enabled
      if (!!this.consolidatedMetrics && this.perfOrchestrator.data.specifiedEntitiesMetrics.length > 0) {
         this.chartContainer.nativeElement.chartData.timestamps =
               this.perfOrchestrator.data.specifiedEntitiesMetrics[0].timeStamps;
      }
   }

   // Fire an event when the zoom level changes
   afterSetExtremesXAxisCallback: Highcharts.AxisSetExtremesEventCallbackFunction = (event: any) => {
      if (event.target.categories.length < 2) {
         // no need to zoom if there are less than 2 points
         return;
      }

      const zoomOption: ChartZoomOption = new ChartZoomOption();
      zoomOption.min = event.min;
      zoomOption.max = event.max;
      zoomOption.dataMin = event.dataMin;
      zoomOption.dataMax = event.dataMax;
      /*
      * Check should be == because when chart is being initializing 'dataMax' and 'dataMin' props are null
      * but 'min' and 'max' are undefined, so check shouldn't be on strong type comparison
      * */
      // tslint:disable-next-line:triple-equals
      if (event.dataMax == event.max && event.dataMin == event.min) {
         zoomOption.isZoomOut = true;
      }

      // tslint:disable-next-line:triple-equals
      if (this.isZoomAllowed && this.zoomOptions != zoomOption) {
         this.zoom.emit(zoomOption);
         this.extremes = zoomOption;
      }
   };

   // Fire an event when the series are hovered
   seriesMouseOverCallback = (event: any) => {
      this.emitHoveredPoint(event, true);
   };

   // Fire an event when the series are hovered out
   seriesMouseOutCallback = (event: any) => {
      this.emitHoveredPoint(event, false);
   };

   private emitHoveredPoint(event: any, isHover: boolean): void {
      this.pointHovered.emit(new SeriesPointHovered(event.target.series.chart.container.id,
            isHover,
            event.target.series.tooltipOptions.shared,
            event.target.series.index,
            event.target.index,
            event.target.category));
   }

   async drawChart(): Promise<void> {
      // Use id to avoid error "RangeError: Maximum callstack exceeded is produced".
      this.options.chart.renderTo = this.chartElement.nativeElement.id;

      // We use clr-accordion to display multiple entities in perf-entities-data.perf.html,
      // when expanding accordion panel, the chart container has not yet been rendered,
      // wait for the chart container and draw the chart if its container exists.
      if (!this.hasChartContainer()) {
         await this.waitForChartContainer();
      }

      // Add description to help screen reader users understand the chart.
      this.options.accessibility = {
         enabled: true,
         description: this.graph.description,
      };

      // Initialize chart
      this.chart = Highcharts.chart(this.options);
      this.chart.getFilename = this.getChartFileName;
      this.refWatcher.watchChartRef(this.chart);
      this.extremes = this.zoomOptions;

      if (this.chartWidth && this.chartHeight) {
         this.chart.setSize(this.chartWidth, this.chartHeight);
      } else if (this.showSmall) {
         // Set small size if chart is in compact mode
         this.chart.setSize(PerfChartComponent.SMALL_CHART_WIDTH, PerfChartComponent.SMALL_CHART_HEIGHT);
      } else if (!!this.consolidatedMetrics) {
         // Set bigger size for consolidated charts because they can contain metrics for up to 10 vms
         this.chart.setSize(this.chartContainer.nativeElement.width, PerfChartComponent.CONSOLIDATED_CHART_HEIGHT);
      }
      this.highchartsService.applyScreenReaderMessage(this.chartElement.nativeElement);
      // Fire an event when the chart is initialized
      this.load.emit(this.chart);
   }

   waitForChartContainer(): Promise<void> {
      if (this.hasChartContainer()) {
         return Promise.resolve();
      }
      return new Promise((resolve: (promise: Promise<void>) => void): void => {
         this.refWatcher.setTimeout(() => {
            resolve(this.waitForChartContainer());
         }, PerfChartComponent.WAIT_FOR_CHART_CONTAINER_TIME);
      });
   }

   hasChartContainer(): boolean {
      // the value of renderTo is the id of chart container.
      return !!document.getElementById(this.options.chart.renderTo.toString());
   }

   ngOnDestroy(): void {
      this.options = null;
      this.chartContainer.nativeElement.chartData = null;
      this.chartContainer = null;
      this.chartElement = null;
   }

   private buildOptions(afterSetExtremesXAxisCallback: Highcharts.AxisSetExtremesEventCallbackFunction,
         customizableThreshold: PerfGraphThreshold,
         seriesMouseOverCallback: Highcharts.SeriesMouseOverCallbackFunction,
         seriesMouseOutCallback: Highcharts.SeriesMouseOutCallbackFunction):
         Highcharts.Options {
      if (this.consolidatedMetrics) {
         return this.loadConsolidatedMetricsChartSettings(afterSetExtremesXAxisCallback,
               customizableThreshold,
               seriesMouseOverCallback,
               seriesMouseOutCallback);
      } else {
         return this.loadSeparatedMetricsChartSettings(afterSetExtremesXAxisCallback,
               customizableThreshold,
               this.metrics,
               seriesMouseOverCallback,
               seriesMouseOutCallback);
      }
   }

   private loadConsolidatedMetricsChartSettings(
         afterSetExtremesXAxisCallback: Highcharts.AxisSetExtremesEventCallbackFunction,
         customizableThreshold: PerfGraphThreshold,
         seriesMouseOverCallback: Highcharts.SeriesMouseOverCallbackFunction,
         seriesMouseOutCallback: Highcharts.SeriesMouseOverCallbackFunction): Highcharts.Options {
      if (VsanUiUtils.isEmpty(this.perfOrchestrator.data.specifiedEntitiesMetrics)) {
         return;
      }

      // Get first entity from the selected and use timestamps for initialize chart XAxis and YAxis
      const firstEntityPerfData: PerfEntityStateData = this.perfOrchestrator.data.specifiedEntitiesMetrics[0];

      const config: ConsolidatedConfigData = {
         entityRefId: this.entityRefId,
         graph: this.graph,
         firstEntityPerfData: firstEntityPerfData,
         consolidatedMetrics: this.consolidatedMetrics,
         entityIdentifierToColor: this.consolidatedEntitiesData.entityIdentifierToColor,
         index: this.index,
         looseXAxisLabel: this.looseXAxisLabel,
         legendPosition: this.legendPosition,
      };

      // Load consolidated chart settings for all selected entities.
      const options: Highcharts.Options = PerfChartUtil.getConsolidatedChartOptions(config,
            afterSetExtremesXAxisCallback,
            customizableThreshold,
            seriesMouseOverCallback,
            seriesMouseOutCallback);

      this.refWatcher.watchSubscriptionRef(this.perfOrchestrator.consolidatedViewHelper.change
            .subscribe(async (data: FlatGraphMetric[]) => {
               if (data == null || VsanUiUtils.isEmpty(data)) {
                  return;
               }

               config.consolidatedMetrics = this.perfOrchestrator.getSpecifiedEntitiesFlatDataByGraph(this.graph);

               this.options = PerfChartUtil.getConsolidatedChartOptions(config,
                     afterSetExtremesXAxisCallback,
                     customizableThreshold,
                     seriesMouseOverCallback,
                     seriesMouseOutCallback);
               this.applyHighChartThemeOptions();

               await this.drawChart();

               // update to QE's chartData with the already styled consolidated metrics
               if (this.chartContainer && this.chartContainer.nativeElement.chartData) {
                  this.chartContainer.nativeElement.chartData.metrics = config.consolidatedMetrics;
               }
            }));

      return options;
   }

   private loadSeparatedMetricsChartSettings(
         afterSetExtremesXAxisCallback: Highcharts.AxisSetExtremesEventCallbackFunction,
         customizableThreshold: PerfGraphThreshold,
         metrics: PerfEntityStateData,
         seriesMouseOverCallback: Highcharts.SeriesMouseOverCallbackFunction,
         seriesMouseOutCallback: Highcharts.SeriesMouseOutCallbackFunction): Highcharts.Options {
      const plotLineCallback: PlotLineClickCallback | null = this.keepPlotLineOnClick
            ? this.onPlotLineClicked
            : null;
      const tooltipActionLink: string = Object.values(PefGraphId).includes(this.graph.id as PefGraphId)
            ? this.actionLink
            : null;
      // Load common chart settings
      return PerfChartUtil.getChartOptions(this.entityRefId, this.graph, metrics, this.index,
            this.looseXAxisLabel, this.legendPosition, afterSetExtremesXAxisCallback, customizableThreshold,
            this.keepPlotLineOnClick,
            plotLineCallback,
            tooltipActionLink,
            seriesMouseOverCallback,
            seriesMouseOutCallback);
   }

   private onPlotLineClicked: PlotLineClickCallback = (point: PerfChartPoint): void => {
      const xAxisIndex: number = point.index;
      this._selectedPlotLineValue = this.metrics.timeStamps[xAxisIndex];
      this.emitOnPlotLineChaged(xAxisIndex);
   };

   private emitOnPlotLineChaged(xAxisIndex: number): void {
      const selectionData: ChartPlotLineSelectionData = new ChartPlotLineSelectionData();
      selectionData.timePoint = this.metrics.timeStamps[xAxisIndex];
      selectionData.seriesValues = this.chart.userOptions.series.map((series: SeriesOptionsType) => {
         const chartSeries: PerfChartSeries = series as any;
         const seriesData: ChartMetricSelectionData = new ChartMetricSelectionData();
         seriesData.metricKey = chartSeries.metricKey;
         seriesData.metricLabel = chartSeries.name;
         seriesData.value = chartSeries.data[xAxisIndex];
         return seriesData;
      });
      this.onPlotLineChanged.emit(selectionData);
   }

   private updatePlotLine = (): void => {
      if (!this._selectedPlotLineValue) {
         return;
      }
      const plotLineIndex: number = this.metrics.timeStamps.indexOf(this._selectedPlotLineValue);
      PerfChartUtil.addPlotLine(this.chart, plotLineIndex);
      this.emitOnPlotLineChaged(plotLineIndex);
   };

   private applyHighChartThemeOptions(): void {
      const chartType: string = this.graph.isHistogram
            ? HighchartsThemeService.HISTOGRAM_CHART
            : HighchartsThemeService.COLUMN_LINE_CHART;
      if (this.options.yAxis && !Array.isArray(this.options.yAxis)) {
         const defaultThemeOptions: Highcharts.Options = this.highchartsThemeService.getThemeOptions(chartType);
         this.options = VsanUiUtils.deepmergeObjects(this.options, defaultThemeOptions);
         return;
      }
      this.options = this.highchartsThemeService.getThemeOptionsWithMultipleYAxis(this.options, chartType);
   }

   exportPNG(): void {
      this.chart.exportChartLocal({type: "image/png"}, null);
   }

   exportJPEG(): void {
      this.chart.exportChartLocal({type: "image/jpeg"}, null);
   }

   exportSVG(): void {
      this.chart.exportChartLocal({type: "image/svg+xml"}, null);
   }

   exportCSV(): void {
      this.chart.downloadCSV.call(this.chart);
   }

   setThreshold(): void {
      const context: SetThresholdContextData = {
         hostRef: ManagedObject.contextObject,
         metricLabel: this.graph.label,
         threshold: this._customizableThreshold,
         graphUnit: this.graph.unit,
      };

      new ModalBuilder("chart/setThreshold")
            .setSize(550, 270)
            .open(context)
            .then(this.refreshOnThresholdChange);
   }

   private refreshOnThresholdChange = async (threshold: PerfGraphThreshold): Promise<void> => {
      this._customizableThreshold = threshold;
      const plotBands: any[] =
            PerfChartUtil.getThresholdBands(
                  PerfChartUtil.getThreshold(this.graph, this.metrics, this._customizableThreshold),
                  this.chart.yAxis[0].max);
      this.chart.yAxis[0].update({plotBands: plotBands});
   };

   private getChartFileName = (): string => {
      return this.graph.label;
   };

   private drawNewPoints(newMetrics: PerfEntityStateData): void {
      const newOptions: any = this.loadSeparatedMetricsChartSettings(
            this.afterSetExtremesXAxisCallback,
            this._customizableThreshold,
            newMetrics,
            this.seriesMouseOverCallback,
            this.seriesMouseOutCallback);

      this.chart.series.forEach((series, index) => {
         const data: number[] = newOptions.series[index].data;
         series.setData(data);
      });
   }

   @HostListener("click", ["$event"])
   clickout(e: any): void {
      if (typeof (e.target.className) !== "string" || !e.target.className.includes("tooltip-action")) {
         return;
      }
      const selectionData: ChartPlotLineSelectionData = new ChartPlotLineSelectionData();
      selectionData.graphId = e.target.attributes.graphid.value;
      selectionData.timePoint = e.target.attributes.timepoint.value;
      this.onTooltipActionClicked.emit(selectionData);
   }
}