import {BasePerfEntity} from "./base-perf-entity";

export class NestedPerfEntity extends BasePerfEntity {
   vmdk: string;

   /**
    * vSCSI controller controls the I/O operation of the disks
    * so each disk of a VM must have a vSCSI controller,
    * when customer checks the virtual disk performance,
    * the vSCSI controller performance data should be displayed together.
    */
   combinedEntity: BasePerfEntity;
}