import {BehaviorSubject} from "rxjs";
import {BasePerfEntity} from "../../../performance/model/base-perf-entity";
import {PerfTimeRangeData} from "@generated/perf-time-range-data";
import {GraphMetric} from "@component/chart/performance/graph-metrics.data";
import {PerfEntityStateData} from "@generated/perf-entity-state-data";
import {PerfGraph} from "@component/chart/performance/perf-graph";
import {PerfChartUtil} from "@component/chart/performance/perf-chart-util";
import {FlatGraphMetric} from "@component/chart/performance/flat-graph-metric";
import {PerfConsolidatedMetricSeries} from "@component/chart/performance/perf-consolidated-metric-series";
import {PerfGraphMetricsData} from "@generated/perf-graph-metrics-data";
import {PerformanceUtils} from "../../../performance/util/perf-utils";
import {DashStyleValue} from "highcharts";

/**
 * Used to indicate type of the events which are managed by perf-orchestrator-service
 */
export enum PerfChangeContext {
   /*
    * Used when view mode is changed. There are 2 modes Average mode (Cluster level metrics)
    * and Specific mode(Show Specific VMs)
    */
   MODE_CHANGED,
   /*
    * Modes have states. For example Specifics mode (Show Specific VMs)
    * has consolidated view state and separate view state
    */
   STATE_CHANGED,
   /*
    * When entities has been changed by filtration or user entities selection
    */
   FILTERED_ENTITIES,
   /*
    * When entities are selected, base entity data is loaded for this selected entities.
    * After that perf data should be loaded and this field is used to indicate that perf data fetching
    */
   METRICS_FETCHED,
   /*
    * When time range picker is changed
    */
   TIME_RANGE_CHANGED
}

/**
 * This class keeps all needed data for multiple entities performance
 * such as base perf objects, graphs, metrics and so on.
 *
 * This data is used in perf-orchestrator-service
 */
export class PerfData {

   constructor(private change: BehaviorSubject<PerfChangeContext>) {}

   /*
   * Parent entity type -> com.vmware.vsan.perf.entity.cluster-domclient
   * For example in Cluster -> Monitor -> Performance this parent entity will be cluster
   */
   parentEntityType: string;

   /*
   * Entities that are children of average entity
   * For example com.vmware.vsan.perf.entity.vm
   */
   entityType: string;

   /**
    * Holds data for all selected entities in order to avoid fetch it again
    */
   entities: BasePerfEntity[] = [];

   /*
   * This object keeps selected entities from user. For these entities performance charts should be displayed.
   */
   selectedEntities: BasePerfEntity[] = [];

   timeRangeData: PerfTimeRangeData;

   /*
   * This objects keep map in order to fast accessing GraphMetric object by entity moRef id
   */
   entitiesGraphDataCache: Map<string, GraphMetric[]> = new Map<string, GraphMetric[]>();

   /*
   * Keeps entity data with all of it's graphs (IOPS, Throughput, Latency) and performance data for given time range
   * For example let's get IOPS, you have "Read IOPS", "Write IOPS" types of graphs.
   * In GraphMetric has entityIdentification(name, id, moRef),
   * graphs for entity (for example Vm entity has IOPS, Throughput and Latency graphs)
   * and data for each of this graphs for selected time range.
   */
   entitiesGraphData: GraphMetric[] = [];

   /*
    * This object is used in order to fast accessing FlatGraphMetric object by entity moRef id
    *
    * Note: FlatGraphMetric keeps entity identification such as id, name, moRef.
    * Also keeps all metrics for specific chart.
    * For example, IOPS chart has 2 types of metrics "Read IOPS" and "Write IOPS".
    */
   flatMetricCacheMap: Map<string, FlatGraphMetric[]> = new Map<string, FlatGraphMetric[]>();

   /*
   * This keeps entity data for one graph
   * For example FlatGraphMetric keeps entity identification object where is stored name, uuid, moRef,
   * graph representation object and entity performance data for this graph
   */
   entitiesFlatData: FlatGraphMetric[] = [];

   /*
   * This map keeps selection state of every loaded entity
   * For example
   * {
   *  vm-51: false,
   *  vm-58: true
   * }
   * This means that vm-58 is selected and vm-51 is not. This map is used in order to load and
   * show perf charts only for selected entities
   */
   entitySelectionStateMap: Map<string, boolean> = new Map<string, boolean>();

   /*
   * This keeps entity types for all managed object types that have performance metrics
   * Example: com.vmware.vsan.perf.entity.cluster-domclient, com.vmware.vsan.perf.entity.vm
   */
   entityTypes: any; // Map<String, VsanPerfEntityType>

   /*
   * This holds performance data which is loaded from perf API when some entities are selected.
   */
   _specifiedEntitiesMetrics: PerfEntityStateData[];

   get specifiedEntitiesMetrics(): PerfEntityStateData[] {
      return this._specifiedEntitiesMetrics;
   }

   set specifiedEntitiesMetrics(value: PerfEntityStateData[]) {
      this._specifiedEntitiesMetrics = value;

      this.entitiesGraphData = value.map((metric: PerfEntityStateData) => {
         const entity: BasePerfEntity = PerformanceUtils.getPerfEntityByUuid(this.entities, metric.entityRefId);
         const graphs: PerfGraph[] =
               entity.configSpec.graphs.map(graph => PerfChartUtil.getGraphDefinition(graph));

         const graphMetric: GraphMetric = new GraphMetric(metric, graphs);

         graphMetric.entityIdentifier = {
            id: entity.entityIdentifier,
            name: entity.entityName,
            refId: metric.entityRefId
         };

         return graphMetric;
      });

      this.entitiesFlatData = [];
      this.flatMetricCacheMap = new Map<string, FlatGraphMetric[]>();
      this.entitiesGraphDataCache = new Map<string, GraphMetric[]>();
      this.entitiesGraphData.forEach((entry: GraphMetric) => {
         if (!this.entitiesGraphDataCache[entry.entityIdentifier.refId]) {
            this.entitiesGraphDataCache[entry.entityIdentifier.refId] = [];
         }
         this.entitiesGraphDataCache[entry.entityIdentifier.refId].push(entry);

         this.entitiesFlatData.push(
               ...entry.graphs.map(graph => new FlatGraphMetric(entry.entityRefId, entry.metrics,
                     graph, entry.entityIdentifier)));

         entry.graphs.forEach((graph: PerfGraph) => {
            if (this.flatMetricCacheMap[graph.id] == null) {
               this.flatMetricCacheMap[graph.id] = [];
            }

            this.flatMetricCacheMap[graph.id].push(...this.getSpecifiedEntitiesFlatDataByGraph(graph));
         });
      });

      this.change.next(PerfChangeContext.METRICS_FETCHED);
   }

   /*
   * This method aggregate loaded performance data for selected entities to more consumable way for perf-chart component
   * Here are set some highcharts options together with graphs and entity data.
   * In other words, method returns all needed data to display chart for concrete entity chart
   */
   getSpecifiedEntitiesFlatDataByGraph = (graph: PerfGraph): PerfConsolidatedMetricSeries[] => {
      if (this.entitiesFlatData == null) {
         return null;
      }

      const consolidatedMetricsForSpecifiedEntities: PerfConsolidatedMetricSeries[] = [];

      const lineStyleTypes: DashStyleValue[] = ["Solid", "ShortDash", "ShortDot", "ShortDashDot"];
      const lineStyleMap: Map<string, DashStyleValue> = new Map<string, DashStyleValue>();

      const legendGraphTypesMap: Array<{key: string, label: string}> = graph.metrics.map(z => {
         return {
            key: z.key,
            label: z.label
         };
      });

      for (let i = 0; i < legendGraphTypesMap.length; i++) {
         lineStyleMap[legendGraphTypesMap[i].key] = lineStyleTypes[i];
         consolidatedMetricsForSpecifiedEntities.push({
            name: legendGraphTypesMap[i].label,
            metricName: legendGraphTypesMap[i].label,
            key: legendGraphTypesMap[i].key,
            entityIdentifier: null,
            data: null,
            isLegend: true,
            lineStyle: lineStyleTypes[i]
         });
      }

      const graphMetricKeys: string[] = graph.metrics.map(metric => metric.key);

      /*
       * Get all flat data that is for metrics which are contained in graphMetricKeys
       *
       * Note: For each entity in specifiedEntitiesFlatData get metrics and check if at least one is contained in
       * graphMetricKeys, graphMetricKeys represents all keys of searched graph's metrics.
       */
      this.entitiesFlatData.filter(
            entity => entity.graph.metrics.find(
                  entityGraphMetric => graphMetricKeys.find(key => key === entityGraphMetric.key) != null))
            .forEach((metric: FlatGraphMetric) => {
               metric.metrics.metricsSeries.forEach((value: PerfGraphMetricsData) => {
                  // Get legend graph by current entity's graph key
                  const graphSet: {key: string, label: string} =
                        legendGraphTypesMap.find(x => x.key === value.key);

                  if (!graphSet) {
                     return;
                  }

                  consolidatedMetricsForSpecifiedEntities.push({
                     name: `${graphSet.label} ${metric.entityIdentifier.name}`,
                     metricName: graphSet.label,
                     key: value.key,
                     entityIdentifier: metric.entityIdentifier,
                     data: value.values,
                     isLegend: false,
                     lineStyle: lineStyleMap[value.key]
                  });
               });
            });

      return consolidatedMetricsForSpecifiedEntities;
   };
}
