import {FlatGraphMetric} from "@component/chart/performance/flat-graph-metric";
import {GraphMetric} from "@component/chart/performance/graph-metrics.data";
import {BehaviorSubject} from "rxjs";
import {BasePerfEntity} from "../../../performance/model/base-perf-entity";
import {PerfChangeContext, PerfData} from "./perf-data";

/**
 * Contains helper logic for consolidated state view
 * It is responsible for checkboxes which are shown in consolidated view
 */
export class ConsolidatedViewHelper {

   /*
   * Keeps visibility state for each of the selected entities
   * Note: Visibility state is changed through checkboxes on consolidated screen
   * */
   entityVisibilityStateMap: any;
   change: BehaviorSubject<FlatGraphMetric[]>;

   /*
   * Keeps entity data with all of it's graphs (IOPS, Throughput, Latency) and performance data for given time range
   */
   graphData: GraphMetric[];

   /*
   * This keeps entity data for one graph
   * For example FlatGraphMetric keeps entity identification object where is stored name, id, moRef,
   * graph representation object and entity performance data for this graph
   */
   flatData: FlatGraphMetric[];

   /**
    *
    * @param entitiesChange -> tracks couple of event for entities filtered, changed, loaded, etc.
    * @param data -> object that keeps all needed loaded data for multiple entities performance (consolidated view)
    */
   constructor(private entitiesChange: BehaviorSubject<PerfChangeContext>, private data: PerfData) {
      this.entityVisibilityStateMap = [];
      this.change = new BehaviorSubject(null);

      //////////////
      // TODO [kaleksandrov] Create a PerfStateHolder or smth that will help you with keeeping the state and easily toggle on/off selection
      /////////////


      entitiesChange.subscribe((context: PerfChangeContext) => {
         if (context === PerfChangeContext.FILTERED_ENTITIES
               || context === PerfChangeContext.STATE_CHANGED
               || context === PerfChangeContext.MODE_CHANGED) {
            if (!data.entities) {
               return;
            }
            const selectedEntities: BasePerfEntity[] = data.entities
                  .filter((entity: BasePerfEntity) => data.entitySelectionStateMap.get(entity.entityIdentifier));
            this.entityVisibilityStateMap = {};
            selectedEntities.forEach((entity: BasePerfEntity) => {
               this.entityVisibilityStateMap[entity.entityIdentifier] = true;
            });

         } else if (context === PerfChangeContext.METRICS_FETCHED) {
            this.graphData = this.data.entitiesGraphData;
            this.flatData = this.data.entitiesFlatData;
            this.data.entitiesGraphData =
                  this.graphData.filter(x => this.entityVisibilityStateMap[x.entityIdentifier.id]);

            // Include only current selected entities graphs' metrics
            this.data.entitiesFlatData =
                  this.flatData.filter(x => this.entityVisibilityStateMap[x.entityIdentifier.id]);
         }
      });
   }

   /*
   * This method is responsible for checkboxes state change action
   */
   toggle(specifiedEntityId: string, isVisible: boolean) {
      // Set if entity should be visible on the charts or not
      this.entityVisibilityStateMap[specifiedEntityId] = isVisible;

      // Include only current selected entities graphs' metrics
      if (this.graphData) {
         this.data.entitiesGraphData =
               this.graphData.filter(x => this.entityVisibilityStateMap[x.entityIdentifier.id]);
      }

      // Include only current selected entities graphs' metrics
      if (this.flatData) {
         this.data.entitiesFlatData =
               this.flatData.filter(x => this.entityVisibilityStateMap[x.entityIdentifier.id]);
      }

      // Pass flat data in order to be captured by some component which is responsible for drawing of the charts
      this.change.next(this.data.entitiesFlatData);
   }
}
