import {Injectable} from "@angular/core";
import {SpaceEfficiencyConfig} from "@generated/space-efficiency-config";
import {VsanUiUtils} from "@util/vsan-util";
import {SpaceEfficiencyCapacityData} from "@generated/space-efficiency-capacity-data";
import {SystemUsageCapacityData} from "@generated/system-usage-capacity-data";
import {VsanCapabilityData} from "@generated/vsan-capability-data";

@Injectable()
export class SpaceEfficiencyService {

   isSpaceEfficiencySupported(clusterCapabilities: VsanCapabilityData): boolean {
      // Space Efficiency is supported if one of these is supported
      return clusterCapabilities.isDeduplicationAndCompressionSupported ||
            clusterCapabilities.isCompressionOnlySupported;
}

   isOnlyAllFlashSupportedForClaimingDisks(spaceEfficiency: SpaceEfficiencyConfig): boolean {
      return spaceEfficiency === SpaceEfficiencyConfig.COMPRESSION
            || spaceEfficiency === SpaceEfficiencyConfig.DEDUPLICATION_AND_COMPRESSION;
   }

   isOnlyAllFlashSupportedForRemovingDisk(spaceEfficiency: SpaceEfficiencyConfig): boolean {
      return spaceEfficiency === SpaceEfficiencyConfig.DEDUPLICATION_AND_COMPRESSION;
   }

   isDiskPreCheckEvacuationSupported(spaceEfficiency: SpaceEfficiencyConfig): boolean {
      return spaceEfficiency === SpaceEfficiencyConfig.NONE
            || spaceEfficiency === SpaceEfficiencyConfig.COMPRESSION;
   }

   toLabel(spaceEfficiency: SpaceEfficiencyConfig): string {
      switch (spaceEfficiency) {
         case SpaceEfficiencyConfig.NONE:
            return VsanUiUtils.getString("configureVsan.settings.spaceEfficiency.noneLabel");
         case SpaceEfficiencyConfig.COMPRESSION:
            return VsanUiUtils.getString("configureVsan.settings.spaceEfficiency.compressionLabel");
         case SpaceEfficiencyConfig.DEDUPLICATION_AND_COMPRESSION:
            return VsanUiUtils.getString("configureVsan.settings.spaceEfficiency.dedupAndCompressionLabel");
         default:
            return null;
      }
   }

   capacityTotalUsedSpace(capacity: SpaceEfficiencyCapacityData, systemUsageCapacity: SystemUsageCapacityData): number {
      return capacity.usedSpaceBefore + systemUsageCapacity.spaceEfficiencyOverhead;
   }

   capacitySavingsLabel(spaceEfficiency: SpaceEfficiencyConfig,
         spaceEfficiencySavings: string,
         spaceEfficiencyRatio: string,
         isVsanMaxEnabled: boolean): string {
      if (this.isCompressionEnabled(spaceEfficiency, isVsanMaxEnabled)) {
         return VsanUiUtils.getString(
               "vsan.monitor.capacity.overview.compressionOnly.label",
               spaceEfficiencySavings,
               spaceEfficiencyRatio);
      } else if (this.isDedupAndCompressionEnabled(spaceEfficiency)) {
         return VsanUiUtils.getString(
               "vsan.monitor.capacity.overview.deduplicationAndCompression.label",
               spaceEfficiencySavings,
               spaceEfficiencyRatio);
      }

      return "";
   }

   // For vSAN Max enabled cluster the space efficiency is policy based and should always be considered.
   private isCompressionEnabled(spaceEfficiency: SpaceEfficiencyConfig, isVsanMaxEnabled: boolean): boolean {
      return spaceEfficiency === SpaceEfficiencyConfig.COMPRESSION || isVsanMaxEnabled;
   }

   private isDedupAndCompressionEnabled(spaceEfficiency: SpaceEfficiencyConfig): boolean {
      return spaceEfficiency === SpaceEfficiencyConfig.DEDUPLICATION_AND_COMPRESSION;
   }

   capacitySpaceOverheadLabel(spaceEfficiency: SpaceEfficiencyConfig): string {
      switch (spaceEfficiency) {
         case SpaceEfficiencyConfig.COMPRESSION:
            return VsanUiUtils.getString(
                  "vsan.monitor.capacity.usage.breakdown.category.system.compressionOnly");
         case SpaceEfficiencyConfig.DEDUPLICATION_AND_COMPRESSION:
            return VsanUiUtils.getString(
                  "vsan.monitor.capacity.usage.breakdown.category.system.deduplicationAndCompression");
         default:
            return "";
      }
   }

   capacityBreakdownLabel(spaceEfficiency: SpaceEfficiencyConfig): string {
      switch (spaceEfficiency) {
         case SpaceEfficiencyConfig.COMPRESSION:
            return VsanUiUtils.getString("vsan.monitor.capacity.usage.breakdown.compressionOnly.title");
         case SpaceEfficiencyConfig.DEDUPLICATION_AND_COMPRESSION:
            return VsanUiUtils.getString("vsan.monitor.capacity.usage.breakdown.deduplicationAndCompression.title");
         case SpaceEfficiencyConfig.NONE:
         default:
            return VsanUiUtils.getString("vsan.monitor.capacity.usage.breakdown.title");
      }
   }
}