import {VsanUiUtils} from "@util/vsan-util";
import {ColumnLineChartData} from "@component/chart/column-line-chart/column-line-chart.data";
import {HistogramColumnChartData} from "@component/chart/histogram-chart/histogram-column-chart.data";
import {TooltipValue} from "@util/tooltip-value";

/**
 * Provide helper utils related to working with custom data for tooltips.
 */
export class HighchartsTooltipUtil {

   static formatTooltipData(tooltipValues: Map<string, string> | Map<string, TooltipValue>): string {
      let tooltipContent: string = "";
      for (const label of tooltipValues.keys()) {
         const tooltip: string | TooltipValue = tooltipValues.get(label);
         if (tooltip instanceof TooltipValue) {
            if (VsanUiUtils.isEmpty(tooltipContent)) {
               tooltipContent = `<table>`;
            }
            tooltipContent += this.formatColorTooltipPair(tooltip, label);
         } else {
            tooltipContent += this.formatTooltipPair(label, tooltip);
         }
      }
      if (tooltipContent.startsWith(`<table>`)) {
         tooltipContent += `</table>`;
      }
      return tooltipContent;
   }

   private static formatColorTooltipPair(tooltip: TooltipValue, label: string): string {
      return `<tr>
                 <td>
                    ${this.createSvgRectangle(tooltip.color)}
                 </td>
                 <td class="text-nowrap">
                    ${label}:
                 </td>
                 <td class="tooltip-value text-nowrap">
                    ${tooltip.value}
                 </td>
              </tr>`;
   }

   private static createSvgRectangle(color: string): string {
      if (!color) {
         return "";
      }
      return `<svg class="tooltip-color-flag" style="fill:${color}">
                <rect x="0" y="4" width="25" height="6"/>
              </svg>`;
   }

   private static formatTooltipPair(label: string, value: string): string {
      return `<span>${label}: ${value}</span><br/>`;
   }

   static formatIndexedTooltipData(series: Highcharts.Series, index: number): string {
      let tooltipContent: string = "";
      const tooltipValues: Map<string, string> = series.options.custom.tooltipValues;
      for (const label of tooltipValues.keys()) {
         const labelValues: string = tooltipValues.get(label);
         tooltipContent += `<span>${label}: ${labelValues[index]}</span><br/>`;
      }
      return tooltipContent;
   }

   static getTooltipHeader(key: number, series: Highcharts.Series, index: number): string {
      if (HighchartsTooltipUtil.hasCustomTooltipHeader(series.options)) {
         return `<b>${series.options.custom.tooltipTitles[index]}</b><br/>`;
      }
      if (HighchartsTooltipUtil.hasSeriesCustomTooltipData(series)) {
         return `<b>${series.name} - ${key}</b><br/>`;
      }
      return `<b>${key}</b><br/>`;
   }

   static hasCustomTooltipHeader(seriesOptions: Highcharts.SeriesOptionsType): boolean {
      return seriesOptions.custom && !VsanUiUtils.isEmpty(seriesOptions.custom.tooltipTitles);
   }

   static hasSeriesCustomTooltipData(series: Highcharts.Series): boolean {
      return series && series.options && series.options.custom && series.options.custom.tooltipValues;
   }

   static getSeriesCustomTooltipData(series: Highcharts.Series): any {
      return series.options.custom.tooltipValue;
   }

   static hasPointCustomTooltipData(points: Highcharts.Point[]): boolean {
      return !VsanUiUtils.isEmpty(points)
            && points[0].series.options.custom
            && points[0].series.options.custom.tooltipValue;
   }

   static getPointCustomTooltipData(points: Highcharts.Point[]): any {
      return points[0].series.options.custom.tooltipValue;
   }

   /**
    * Extracts the tooltip data and transform it to Array as the Map cannot be serialized when passed as html property
    */
   static getColumnLineTestingTooltipData(columnChartData: ColumnLineChartData[]):
         MultiTestTooltipData[] {
      if (VsanUiUtils.isEmpty(columnChartData)) {
         return [];
      }
      const result: MultiTestTooltipData[] = [];
      columnChartData.forEach((chartData: ColumnLineChartData) => {
         const multiTooltipData: MultiTestTooltipData = new MultiTestTooltipData(chartData.seriesName);
         const labelsData: Map<string, string[]> = new Map<string, string[]>();
         chartData.tooltipData.forEach((values: string[], key: string) => {
            if (!labelsData.has(key)) {
               labelsData.set(key, values);
            }
         });
         multiTooltipData.values = HighchartsTooltipUtil.mapToTypedArray(labelsData);
         result.push(multiTooltipData);
      });
      return result;
   }

   static getHistogramTestingTooltipData(chartData: HistogramColumnChartData[]): TestTooltipData[] {
      if (VsanUiUtils.isEmpty(chartData)) {
         return [];
      }
      const labelsData: Map<string, string[]> = new Map<string, string[]>();
      chartData.forEach((histogramData: HistogramColumnChartData) => {
         histogramData.labelData.forEach((value: string, key: string) => {
            if (!labelsData.has(key)) {
               labelsData.set(key, []);
            }
            labelsData.get(key).push(value);
         });
      });
      return HighchartsTooltipUtil.mapToTypedArray(labelsData);
   }

   private static mapToTypedArray(tooltipData: Map<string, string[]>): TestTooltipData[] {
      const result: TestTooltipData[] = [];
      tooltipData.forEach((values: string[], key: string) => {
         result.push({key, values});
      });
      return result;
   }
}

/**
 * Holds tooltips for multiple chart series.
 */
class MultiTestTooltipData {
   key: string;
   values: TestTooltipData[];

   constructor(key: string) {
      this.key = key;
   }
}

/**
 * Holds tooltips for a single chart series.
 */
class TestTooltipData {
   key: string;
   values: string[];
}
