import {Component, OnInit} from "@angular/core";
import {IdGenerator} from "@util/id-generator";
import {VsanUiUtils} from "@util/vsan-util";
import {SetThresholdContextData} from "@component/chart/performance/set-threshold-context.data";
import {PerfGraphThreshold} from "@generated/perf-graph-threshold";
import {ManagedObject} from "@service/managed-object";
import {NetworkDiagnosticsService} from "@generated/network-diagnostics-service";
import {Logger} from "@util/logger";
import {PerfChartUtil} from "@component/chart/performance/perf-chart-util";
import {Icon} from "@util/icon";

@Component({
   templateUrl: "set-threshold-dialog.html",
   selector: "vsan-set-threshold-dialog",
   styleUrls: ["set-threshold-dialog.scss"],
})
export class SetThresholdDialogComponent implements OnInit {
   Icon: typeof Icon = Icon;

   id: string;
   hostRef: ManagedObject;
   alertMessages: string[] = [];
   busy: boolean;

   threshold: Threshold;

   readonly WARNING_THRESHOLD_CONTROL_NAME: string = "warningThreshold";
   readonly ERROR_THRESHOLD_CONTROL_NAME: string = "errorThreshold";
   readonly ERRORS: any = {
      [this.WARNING_THRESHOLD_CONTROL_NAME]: "",
      [this.ERROR_THRESHOLD_CONTROL_NAME]: "",
   };

   constructor(private networkDiagnosticsProvider: NetworkDiagnosticsService) {}

   ngOnInit(): void {
      this.id = IdGenerator.getGuid();
      this.hostRef = this.context.hostRef;
      this.setThreshold(this.context.threshold);
   }

   private setThreshold(threshold: PerfGraphThreshold): void {
      if (this.context.graphUnit === PerfChartUtil.PERMILLE_TYPE) {
         this.threshold = new PermilleThreshold(this.context.threshold);
      } else {
         this.threshold = new NumberThreshold(this.context.threshold);
      }
   }

   validate(thresholdControlName: string, value: number): void {
      this.ERRORS[thresholdControlName] = "";

      if (this.isThresholdOutOfRange(value)) {
         this.ERRORS[thresholdControlName] =
               VsanUiUtils.getString("vsan.monitor.performance.chart.setThreshold.input.range.error",
                     this.threshold.minValue, this.threshold.maxValue);
      } else if (this.threshold.warningThreshold >= this.threshold.errorThreshold) {
         this.ERRORS[this.WARNING_THRESHOLD_CONTROL_NAME] =
               VsanUiUtils.getString("vsan.monitor.performance.chart.setThreshold.input.order.error");
      }
   }

   private isThresholdOutOfRange(value: number): boolean {
      return value == null ||
            value < this.threshold.minValue ||
            value > this.threshold.maxValue;
   }

   onThresholdChange(): void {
      this.validate(this.WARNING_THRESHOLD_CONTROL_NAME, this.threshold.warningThreshold);
      this.validate(this.ERROR_THRESHOLD_CONTROL_NAME, this.threshold.errorThreshold);
   }

   onConfirm(): void {
      this.busy = true;
      this.alertMessages = [];
      const thresholdForSave: PerfGraphThreshold = this.threshold.getThresholdForSave();

      this.networkDiagnosticsProvider.setPerfGraphThreshold(this.hostRef, thresholdForSave).then(() => {
         VsanUiUtils.closeModalDialog(thresholdForSave);
      }).catch((err) => {
         Logger.error("Failed to save the network diagnostics thresholds, ", err);
         this.alertMessages.push(VsanUiUtils.getString("vsan.monitor.performance.chart.setThreshold.error.msg"));
      }).finally(() => {
         this.busy = false;
      });
   }

   get context(): SetThresholdContextData {
      return VsanUiUtils.getModalContext() as SetThresholdContextData;
   }

   get haveErrors(): boolean {
      return !!this.ERRORS[this.WARNING_THRESHOLD_CONTROL_NAME] || !!this.ERRORS[this.ERROR_THRESHOLD_CONTROL_NAME];
   }
}

interface Threshold {
   minValue: number;
   maxValue: number;
   warningThreshold: number;
   errorThreshold: number;
   warningThresholdLabel: string;
   errorThresholdLabel: string;
   labelColumnStyle: string;
   getThresholdForSave(): PerfGraphThreshold;
}

class NumberThreshold implements Threshold {
   minValue: number = 1;
   maxValue: number = 1000;
   warningThreshold: number;
   errorThreshold: number;
   warningThresholdLabel: string = VsanUiUtils.getString("vsan.monitor.performance.chart.setThreshold.warningThreshold");
   errorThresholdLabel: string = VsanUiUtils.getString("vsan.monitor.performance.chart.setThreshold.errorThreshold");
   labelColumnStyle: string = "clr-col-5";

   perfGraphThreshold: PerfGraphThreshold;

   constructor(perfGraphThreshold: PerfGraphThreshold) {
      this.perfGraphThreshold = perfGraphThreshold;
      this.warningThreshold = perfGraphThreshold.yellow;
      this.errorThreshold = perfGraphThreshold.red;
   }

   getThresholdForSave(): PerfGraphThreshold {
      const result: PerfGraphThreshold = VsanUiUtils.deepClone(this.perfGraphThreshold);
      result.yellow = this.warningThreshold;
      result.red = this.errorThreshold;
      return result;
   }
}

class PermilleThreshold implements Threshold {
   minValue: number = 0.1;
   maxValue: number = 100;
   warningThreshold: number;
   errorThreshold: number;
   warningThresholdLabel: string = VsanUiUtils.getString("vsan.monitor.performance.chart.setThreshold.warningThreshold.percent");
   errorThresholdLabel: string = VsanUiUtils.getString("vsan.monitor.performance.chart.setThreshold.errorThreshold.percent");
   labelColumnStyle: string = "clr-col-6";

   perfGraphThreshold: PerfGraphThreshold;

   constructor(perfGraphThreshold: PerfGraphThreshold) {
      this.perfGraphThreshold = perfGraphThreshold;
      const thresholdInPercentage: PerfGraphThreshold = PerfChartUtil.toPercentageThreshold(perfGraphThreshold);

      this.warningThreshold = thresholdInPercentage.yellow;
      this.errorThreshold = thresholdInPercentage.red;
   }

   getThresholdForSave(): PerfGraphThreshold {
      const result: PerfGraphThreshold = VsanUiUtils.deepClone(this.perfGraphThreshold);
      result.yellow = this.warningThreshold;
      result.red = this.errorThreshold;
      return PerfChartUtil.toPermilleThreshold(result);
   }
}


