import {Injectable} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {HighchartsThemeService} from "./highcharts-theme.service";
import {HighchartsTooltipUtil} from "@component/chart/highcharts-tooltip.util";

@Injectable()
export class HighchartsPropertiesService {

   static readonly BAR_CHART: string = "bar";
   static readonly PIE_CHART: string = "pie";
   static readonly COLUMN_CHART: string = "column";
   static readonly LINE_CHART: string = "line";

   static readonly TOOLTIP_FONT_SIZE: string = "11px";
   static readonly DEFAULT_FONT_SIZE: string = "13px";
   static readonly DEFAULT_FONT_FAMILY: string = "metropolis";

   static readonly TOOLTIP_LINE_HEIGHT: string = "16px";

   /**
    * Provides the basic common settings for any highchart.
    * The return type is declared as "any" because some of the allowed settings in the chart are missing from
    * Highcharts.Options interface, which causes the build to break, even though they are valid and applied at runtime.
    */
   private options: any;

   constructor(private highchartsThemeService: HighchartsThemeService) {
      this.initBasicChartOptions();
   }

   /**
    * Gets the specific settings for highcharts 'bar' chart.
    * Mouse handlers are optional as they must be set on initial chart render only.
    */
   getBarChartOptions(mouseOverEvent?: Highcharts.SeriesMouseOverCallbackFunction,
         mouseOutEvent?: Highcharts.SeriesMouseOutCallbackFunction): any {
      const barChartOptions: any = {
         chart: {
            type: HighchartsPropertiesService.BAR_CHART,
            height: 30,
            margin: [0, 1, 0, 1],
            spacing: 0,
         },
         yAxis: {
            // By default we hide the yAxis(set to false in the basic options), however for
            // plot lines to work in bar charts we need the axis to be visible.
            visible: true,
            // To make the axis line "invisible", since we need to keep "visible:true"(see above why),
            // we use line width 0 here.
            gridLineWidth: 0,
            // See https://stackoverflow.com/questions/16186427/highcharts-stacked-series-order-is-reversed
            reversedStacks: false,
            // Make sure the bar chart is spread within its container, if set to true sometimes a gap appears
            endOnTick: false,
            // This is calculated as % of the chart area.
            // Allow some very small padding to avoid truncating the borders for stacked-bar chart
            minPadding: 0.005,
            maxPadding: 0.005,
            labels: {
               // See above "visible" is set to true for yAxis, but we don't want anything from the axis to
               // appear, just the plot lines, thus set this to "enabled: false".
               enabled: false,
            },
            tickPositioner: function (): any[] {
               return [0, VsanUiUtils.formatFileSize(this.dataMax)];
            },
         },
         plotOptions: {
            bar: {
               pointWidth: 20,
            },
            series: {
               animation: false,
               borderWidth: 0,
               stacking: "normal", // This makes the bar a stacked one.
               states: {
                  hover: {
                     enabled: false,
                  },
                  inactive: {
                     opacity: 1,
                  },
               },
            },
         },
         tooltip: {
            formatter: function (): string {
               let tooltipData: any;
               if (HighchartsTooltipUtil.hasSeriesCustomTooltipData(this.series)) {
                  tooltipData = HighchartsTooltipUtil.getSeriesCustomTooltipData(this.series);
               } else if (HighchartsTooltipUtil.hasPointCustomTooltipData(this.points)) {
                  // The series are shared, use the custom specified tooltip data
                  tooltipData = HighchartsTooltipUtil.getPointCustomTooltipData(this.points);
               } else {
                  tooltipData = this.y;
               }
               if (tooltipData instanceof Map) {
                  return HighchartsTooltipUtil.formatTooltipData(tooltipData);
               } else {
                  return this.series.name + ": " + VsanUiUtils.formatFileSize(tooltipData);
               }
            },
         },
      };
      if (mouseOverEvent || mouseOutEvent) {
         barChartOptions.plotOptions.series.events = {};
         if (mouseOverEvent) {
            barChartOptions.plotOptions.series.events.mouseOver = mouseOverEvent;
         }
         if (mouseOutEvent) {
            barChartOptions.plotOptions.series.events.mouseOut = mouseOutEvent;
         }
      }
      const mergedOptions: any = VsanUiUtils.deepmergeObjects(this.options, barChartOptions);
      return VsanUiUtils.deepmergeObjects(mergedOptions,
            this.highchartsThemeService.getThemeOptions(HighchartsThemeService.BAR_CHART));
   }

   /**
    * Provides options specific for pie charts
    */
   getPieChartOptions(onPointClick: (event: any) => void): any {
      const pieChartOptions: any = {
         chart: {
            type: HighchartsPropertiesService.PIE_CHART,
            margin: [0, 1, 0, 1],
            spacing: 0,
         },
         plotOptions: {
            pie: {
               allowPointSelect: true,
               slicedOffset: 0,
               size: "75%",
               cursor: "pointer",
               dataLabels: {
                  allowOverlap: false,
                  enabled: true,
                  style: {
                     fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
                     fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
                     fontWeight: "normal",
                     // Issue - pie data labels: unwanted text shadow when setting a hex color:
                     // https://github.com/highcharts/highcharts/issues/4010#issuecomment-298756176
                     textOutline: "0px",
                  },
                  format: "{point.name}",
                  distance: 30,
                  softConnector: false,
               },
               borderWidth: 2,
            },
            series: {
               animation: {
                  duration: 500,
               },
               point: {
                  events: {
                     // tslint:disable-next-line:no-empty
                     click: (event: any) => {
                     },
                  },
               },
            },
         },
         tooltip: {
            formatter: function (): string {
               if (this.point.custom && this.point.custom.tooltipValue instanceof Map) {
                  return HighchartsTooltipUtil.formatTooltipData(this.point.custom.tooltipValue);
               }
               return "<strong>" + VsanUiUtils.formatFileSize(this.y) + "</strong>";
            },
         },
      };

      if (onPointClick) {
         pieChartOptions.plotOptions.series.point.events.click = onPointClick;
      }
      const mergedOptions: any = VsanUiUtils.deepmergeObjects(this.options, pieChartOptions);
      return VsanUiUtils.deepmergeObjects(mergedOptions,
            this.highchartsThemeService.getThemeOptions(HighchartsThemeService.PIE_CHART));
   }

   /**
    * Gets the common settings for a "histogram" chart that uses the "column" chart option.
    * This is the way to show data that has been aggregated in advance
    * as opposed to the raw 'histogram' chart which operates on raw data.
    */
   getColumnHistogramChartOptions(): any {
      const columnChartOptions: any = {
         chart: {
            type: HighchartsPropertiesService.COLUMN_CHART,
            height: 300,
            marginRight: 0,
         },
         xAxis: {
            type: "category",
            // Column chart requires its x-axis, so make it visible
            visible: true,
            // Initialize the categories array, so that it can be populated later without having to create it
            categories: [],
            // Init the title object, so it can be populated later without having to create it
            title: {
               text: "",
               style: {
                  fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
                  fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
               },
            },
            labels: {
               style: {
                  fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
                  fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
               },
            },
         },
         yAxis: {
            // Column chart requires its y-axis, so make it visible
            visible: true,
            // Init the title object, so it can be populated later without having to create it
            title: {
               text: "",
               style: {
                  fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
                  fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
               },
            },
            labels: {
               format: "{value}",
               style: {
                  fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
                  fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
               },
            },
         },
         plotOptions: {
            column: {
               // Used to show the data labels between the columns, not in the middle. E.g. fake a histogram bucket.
               pointPlacement: "between",
               // Make the column width take the full space with small spacing between them.
               pointPadding: 0.01,
               groupPadding: 0,
               borderWidth: 0,
               // No animation on initial load.
               animation: false,
            },
            series: {
               events: {
                  legendItemClick: (e) => {
                     e.preventDefault();
                  },
               },
            },
         },
         legend: {
            // Show the highcharts default legend
            enabled: true,
            y: 10,
            padding: 0,
            itemStyle: {
               fontWeight: "normal",
               fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
               fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
               cursor: "default",
            },
         },
         tooltip: {
            formatter: function (): string {
               let tooltip: string = `<span><b>${this.key}</b></span><br/>`;
               if (this.point.custom && this.point.custom.tooltipValues) {
                  tooltip += HighchartsTooltipUtil.formatTooltipData(this.point.custom.tooltipValues);
               } else {
                  tooltip += `${this.series.name}: ${this.y}`;
               }
               return tooltip;
            },
         },
      };
      const mergedOptions: any = VsanUiUtils.deepmergeObjects(this.options, columnChartOptions);
      return VsanUiUtils.deepmergeObjects(mergedOptions,
            this.highchartsThemeService.getThemeOptions(HighchartsThemeService.HISTOGRAM_COLUMN_CHART));
   }

   /**
    * Gets the common settings for a line chart, and it can be used in column-line chart too.
    */
   getColumnLineChartOptions(): any {
      const columnLineChartOptions: Highcharts.Options = {
         chart: {
            type: HighchartsPropertiesService.LINE_CHART,
            height: 300,
            marginRight: 0,
         },
         // Init the subtitle object in case we want to use it with the chart.
         subtitle: {
            text: "",
            style: {
               fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
               fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
            },
         },
         // The line chart requires its x-axis and y-axis, so make them visible
         xAxis: {
            visible: true,
            labels: {
               style: {
                  fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
                  fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
               },
            },
            crosshair: {
               width: 1,
            },
         },
         yAxis: {
            visible: true,
            // Init the title object, so it can be populated later without having to create it
            title: {
               text: "",
               style: {
                  fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
                  fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
               },
            },
            labels: {
               format: "{value}",
               style: {
                  fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
                  fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
                  whiteSpace: "nowrap",
               },
            },
         },
         plotOptions: {
            column: {
               // Using this property to control the columns of each category to
               // be displayed in stack style, not one besides one.
               stacking: "normal",
               pointWidth: 30,
               borderWidth: 0,
            },
            line: {
               // No animation on initial load.
               animation: false,
            },
            series: {
               // Hide the point marker, only visible on hover.
               marker: {
                  enabled: false,
               },
               events: {
                  legendItemClick: (e) => {
                     e.preventDefault();
                  },
               },
            },
         },
         legend: {
            // Show the highcharts default legend
            enabled: true,
            itemStyle: {
               fontWeight: "normal",
               fontSize: HighchartsPropertiesService.DEFAULT_FONT_SIZE,
               fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
            },
         },
         tooltip: {
            formatter: function (): string {
               let tooltip: string = HighchartsTooltipUtil.getTooltipHeader(this.key,
                     this.series, this.point.index);

               if (HighchartsTooltipUtil.hasSeriesCustomTooltipData(this.series)) {
                  tooltip += HighchartsTooltipUtil.formatIndexedTooltipData(
                        this.series,
                        this.point.index);
               } else {
                  tooltip += `${this.series.name}: ${this.y}`;
               }
               return tooltip;
            },
         },
      };
      const mergedOptions: any = VsanUiUtils.deepmergeObjects(this.options, columnLineChartOptions);
      return VsanUiUtils.deepmergeObjects(mergedOptions,
            this.highchartsThemeService.getThemeOptions(HighchartsThemeService.COLUMN_LINE_CHART));
   }

   private initBasicChartOptions(): void {
      this.options = {
         title: {
            // Don't show title on the chart
            text: null,
         },
         exporting: {
            enabled: false,
            // Keep this to avoid NPE when using TAB on the keyboard
            accessibility: {
               enabled: false,
            },
         },
         credits: {
            // Don't show the highcharts logo and link on the chart
            enabled: false,
         },
         legend: {
            // Don't show the highcharts default legend
            enabled: false,
         },
         // Hide X and Y axis lines and labels
         xAxis: {
            visible: false,
            title: false,
         },
         yAxis: {
            visible: false,
            title: false,
         },
         chart: {
            plotBackgroundColor: null,
            plotBorderWidth: null,
            plotShadow: false,
            // This function is needed because the chart's sections are dynamically calculated.
            // Without it the chart doesn't render correctly.
            events: {
               // tslint:disable-next-line:only-arrow-functions
               load: (event) => {
                  event.target.reflow();
               },
            },
         },
         plotOptions: {
            series: {
               clip: false,
            },
         },
         tooltip: {
            useHTML: true,
            outside: true,
            enabled: true,
            hideDelay: 0,
            backgroundColor: "#FFFFFF",
            style: {
               fontSize: HighchartsPropertiesService.TOOLTIP_FONT_SIZE,
               fontFamily: HighchartsPropertiesService.DEFAULT_FONT_FAMILY,
               lineHeight: HighchartsPropertiesService.TOOLTIP_LINE_HEIGHT,
            },
         },
      };
   }

   getAccessibilityConfig(enableKeyboardNavigation: boolean, description: string): any {
      return {
         keyboardNavigation: {
            enabled: enableKeyboardNavigation,
         },
         screenReaderSection: {
            beforeChartFormat: description,
         },
      };
   }

   getSeriesAccessibilityConfig(): any {
      return {
         exposeAsGroupOnly: true,
      };
   }

   getAccessibilityLangConfig(): any {
      return {
         accessibility: {
            chartContainerLabel: VsanUiUtils.getString(
                  "vsan.screenReader.message.highcharts.nonInteractive.barchart.containerLabel"),
            screenReaderSection: {
               endOfChartMarker: VsanUiUtils.getString(
                     "vsan.screenReader.message.highcharts.nonInteractive.barchart.endOfChartMarker"),
            },
            svgContainerLabel: VsanUiUtils.getString(
                  "vsan.screenReader.message.highcharts.nonInteractive.barchart.svgContainerLabel"),
         },
      };
   }

   getPlotLine(id: string, label: string, value: number, color: string): any {
      // Store the original label from the series here, as adding the tooltip overrides it,
      // then restore the value on mouseout event.
      let originalLabel: string;

      // Create plot line using the id, label, value and color.
      return {
         id: id,
         value: value,
         color: color,
         width: 3, // How thick the plot line to appear on the chart.
         zIndex: 5, // If not specified, the plot line is displayed bellow the bar chart(very clever...)
         events: {
            // Copy/paste from http://url/6wd2, e.g. don't ask me what is going on here....
            // I added comments to the code that I added, the rest is a big mistery.
            mouseover: function (e: MouseEvent): void {
               // Note that "this" points to the plotLine here
               if (!this.axis || !this.axis.series || this.axis.series.length === 0) {
                  // Sanity check to avoid exceptions
                  return;
               }
               const series: any = this.axis.series[0];
               const chart: any = series.chart;
               const POINT_CLASS: any = series.pointClass;
               const tooltip: any = chart.tooltip;
               const point: any = (new POINT_CLASS()).init(series, ["Plotline", this.options.value]);
               // Change the series name to the specified label, but first store the current value.
               originalLabel = point.series.name;
               point.series.name = label;
               // Change the series color to the one used for the plot line
               point.series.color = color;
               const normalizedEvent: any = chart.pointer.normalize(e);

               point.tooltipPos = [
                  normalizedEvent.chartX - chart.plotLeft,
                  normalizedEvent.chartY - chart.plotTop,
               ];
               tooltip.refresh(point);
            },
            mouseout: function (e: MouseEvent): void {
               if (!this.axis || !this.axis.series || this.axis.series.length === 0) {
                  // Sanity check to avoid exceptions
                  return;
               }
               // Hide the tooltip and restore the label for the series to the original value.
               this.axis.chart.tooltip.hide();
               this.axis.series[0].name = originalLabel;
            },
         },
      };
   }

   /**
    * Add message to let screen reader users know how to navigate the chart points with keyboard.
    * Details https://jira.eng.vmware.com/browse/VPAT-2463
    */
   applyScreenReaderMessage(chartElement: any): void {
      let a11yLabel: string = chartElement.getAttribute("aria-label") || "";
      a11yLabel += VsanUiUtils.getString("vsan.monitor.performance.chart.a11y.screenReader.label");
      chartElement.setAttribute("aria-label", a11yLabel);
   }
}
